@import url(./assets/css/material-icons.min.css);
*,
::after,
::before {
  box-sizing: border-box; }

html,
body,
ul,
ol {
  margin: 0;
  padding: 0; }

body,
html {
  width: 100%;
  height: 100%;
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #576264;
  text-align: center; }

#root {
  width: 100%;
  min-height: 100%;
  margin: 0;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: #fd746a;
  background: linear-gradient(to bottom, #fd746a, #fd9067); }
  #root .container {
    width: 920px;
    height: auto;
    min-height: 650px;
    margin: auto;
    padding: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    box-shadow: 0 0 2rem rgba(51, 51, 51, 0.5); }

/* Medium and Small devices (phones & tablets) */
@media (max-width: 992px) {
  #root .container {
    width: 100%;
    padding: 20px; } }

@media (max-height: 680px) {
  #root .container {
    min-height: 95%;
    flex: 1 1 100%; } }

.btn-icon {
  padding: 0;
  border: none;
  background: none; }
  .btn-icon .icon {
    font-size: 36px;
    color: #576264; }
  .btn-icon:hover {
    cursor: pointer; }
    .btn-icon:hover .icon {
      color: #262b2f; }



